<template>
  <!--这个组件用于对底框综合管理，在此页面可以修改底框颜色大小-->
  <div id="tab-bar">
    <slot></slot>  <!--运用插槽，将底框和各小功能分开-->
  </div>
</template>

<script>
    export default {
        name: "TabBar"
    }
</script>

<style scoped>

  #tab-bar{
    display: flex;
    background-color: #f6f6f6;
    box-shadow: 0 -3px 1px rgba(100,100,100,.1);
    /*定位最低*/
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }

</style>



