<template>
    <div class="tab-bar-item" @click="isClicked()">
      <!--<img src="../../assets/img/tabbar/home.svg" alt="">
      <div >首页</div>-->
      <slot v-if="!isActive" name="item-icon"></slot>
      <slot v-else name="item-icon-active"></slot>
      <div :style="activeStyle"><slot name="item-text"></slot></div>
    </div>
</template>

<script>
  //功能插槽组件
    export default {
      name: "TabBarItem",
      props:{
        path: String,
        activeColor:{
          type:String,
          default:'red'
        }
      },

      computed:{
        isActive(){
            return this.$route.path.indexOf(this.path) !== -1      //组件路径
          },
        activeStyle(){
          return this.isActive ? {color:this.activeColor} :{}      //默认传入字体颜色
        }
      },
      methods:{
          isClicked(){
            this.$router.replace(this.path)
          }
      }
    }
</script>

<style scoped>
  .tab-bar-item{
    flex: 1;
    text-align: center;
    height: 49px;
    font-size: 14px;
  }
  .tab-bar-item img{
    height: 24px;
    width: 24px;
    margin-top: 3px;
    vertical-align: middle;
    margin-bottom: 2px;
  }

</style>



